"use client"

import type React from "react"

import { useState, useEffect } from "react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Textarea } from "@/components/ui/textarea"
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter } from "@/components/ui/dialog"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { generateId, saveContact } from "@/lib/store"
import type { Contact } from "@/lib/types"

interface ContactFormDialogProps {
  open: boolean
  onOpenChange: (open: boolean) => void
  existingContact?: Contact
  onSave?: () => void
}

export function ContactFormDialog({ open, onOpenChange, existingContact, onSave }: ContactFormDialogProps) {
  const [name, setName] = useState("")
  const [email, setEmail] = useState("")
  const [phone, setPhone] = useState("")
  const [company, setCompany] = useState("")
  const [notes, setNotes] = useState("")
  const [contactType, setContactType] = useState<"Agent" | "Direct" | "Other">("Other")

  useEffect(() => {
    if (existingContact) {
      setName(existingContact.name)
      setEmail(existingContact.email)
      setPhone(existingContact.phone || "")
      setCompany(existingContact.company || "")
      setNotes(existingContact.notes || "")
      setContactType((existingContact as any).contact_type || "Other")
    } else {
      resetForm()
    }
  }, [existingContact, open])

  const resetForm = () => {
    setName("")
    setEmail("")
    setPhone("")
    setCompany("")
    setNotes("")
    setContactType("Other")
  }

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    const now = new Date().toISOString()

    const contact: Contact & { contact_type?: string } = {
      id: existingContact?.id || generateId(),
      name,
      email,
      phone: phone || undefined,
      company: company || undefined,
      notes: notes || undefined,
      contact_type: contactType,
      createdAt: existingContact?.createdAt || now,
      updatedAt: now,
    }
    saveContact(contact)

    onOpenChange(false)
    resetForm()
    onSave?.()
  }

  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent className="bg-card">
        <DialogHeader>
          <DialogTitle className="text-foreground">{existingContact ? "Edit Contact" : "Add New Contact"}</DialogTitle>
        </DialogHeader>
        <form onSubmit={handleSubmit}>
          <div className="grid gap-4 py-4">
            <div className="grid gap-4 md:grid-cols-2">
              <div className="space-y-2">
                <Label htmlFor="name" className="text-foreground">
                  Name *
                </Label>
                <Input
                  id="name"
                  value={name}
                  onChange={(e) => setName(e.target.value)}
                  required
                  className="bg-secondary"
                />
              </div>
              <div className="space-y-2">
                <Label htmlFor="email" className="text-foreground">
                  Email *
                </Label>
                <Input
                  id="email"
                  type="email"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  required
                  className="bg-secondary"
                />
              </div>
            </div>
            <div className="grid gap-4 md:grid-cols-2">
              <div className="space-y-2">
                <Label htmlFor="phone" className="text-foreground">
                  Phone
                </Label>
                <Input id="phone" value={phone} onChange={(e) => setPhone(e.target.value)} className="bg-secondary" />
              </div>
              <div className="space-y-2">
                <Label htmlFor="company" className="text-foreground">
                  Company
                </Label>
                <Input
                  id="company"
                  value={company}
                  onChange={(e) => setCompany(e.target.value)}
                  className="bg-secondary"
                />
              </div>
            </div>
            <div className="space-y-2">
              <Label htmlFor="contact_type" className="text-foreground">
                Contact Type *
              </Label>
              <Select value={contactType} onValueChange={(value: "Agent" | "Direct" | "Other") => setContactType(value)}>
                <SelectTrigger className="bg-secondary">
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="Agent">Agent</SelectItem>
                  <SelectItem value="Direct">Direct</SelectItem>
                  <SelectItem value="Other">Other</SelectItem>
                </SelectContent>
              </Select>
            </div>
            <div className="space-y-2">
              <Label htmlFor="notes" className="text-foreground">
                Notes
              </Label>
              <Textarea
                id="notes"
                value={notes}
                onChange={(e) => setNotes(e.target.value)}
                placeholder="Add any notes about this contact..."
                className="bg-secondary"
              />
            </div>
          </div>
          <DialogFooter>
            <Button type="button" variant="outline" onClick={() => onOpenChange(false)}>
              Cancel
            </Button>
            <Button type="submit">{existingContact ? "Update Contact" : "Add Contact"}</Button>
          </DialogFooter>
        </form>
      </DialogContent>
    </Dialog>
  )
}
